import {
  useFinanceDeleteFeesStructure,
  useFinanceUpdateFeesStructure,
  useFinanceUpdateRetroFeesStructure,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import AddFeeStructure from "./AddFeeStructure";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import style from "../../Income/Income.module.css";
const FeeStructureDetailMenu = ({
  fid,
  instituteId,
  did,
  openMenu,
  onClose,
  flowAs,
  onCloseParent,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [state, setState] = useState({
    query: "",
  });
  const [financeDeleteFeesStructure] = useFinanceDeleteFeesStructure();
  const [financeUpdateFeesStructure] = useFinanceUpdateFeesStructure();
  const [financeUpdateRetroFeesStructure] =
    useFinanceUpdateRetroFeesStructure();
  const [edit, setEdit] = useState(false);
  const [isDelete, setIsDelete] = useState(false);

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onDelete = () => {
    let validation = requiredField(state);
    if (existingRequiredField(validation)) {
      if (state.query === "Confirm" && openMenu?._id && flowAs) {
        setDisabled((pre) => !pre);
        financeDeleteFeesStructure({
          flow: "Finance_Manager",
          sid: openMenu?._id,
        })
          .then(() => {
            onRefetch();
            onCloseParent();
            setDisabled((pre) => !pre);
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val, asEdit) => {
    if (openMenu?._id) {
      setDisabled((pre) => !pre);
      setEdit(false);
      if (asEdit === "RETRO") {
        financeUpdateRetroFeesStructure({
          sid: openMenu?._id,
          flow: flowAs,
          updateStructure: {
            ...val,
            department: did,
          },
        })
          .then(() => {
            onClose();
            setDisabled((pre) => !pre);
          })
          .catch({});
      } else {
        financeUpdateFeesStructure({
          sid: openMenu?._id,
          flow: "Finance_Manager",
          updateStructure: val,
        })
          .then(() => {
            onClose();
            setDisabled((pre) => !pre);
          })
          .catch({});
      }
    }
  };
  return (
    <>
      {isDelete ? (
        <Modal onClose={() => setIsDelete(false)}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("are_your_sure_to_delete_fee_structure")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("type_confirm_to_continue")}
                placeholder={t("type_confirm_to_continue")}
                name="query"
                value={state.query}
                type="text"
                onChange={onInputChange}
                errorShow={errorField["query"]}
              />
              <CreateButton label="confirm" onAction={onDelete} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      ) : !edit ? (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={onOpenEdit}>{t("edit")}</h6>
          <h6 onClick={() => setIsDelete(true)}>{t("delete")}</h6>
        </ModalMenu>
      ) : (
        <AddFeeStructure
          onClose={onOpenEdit}
          fid={fid}
          openAs="EDIT"
          openStructureMenu={openMenu}
          onEdit={onEdit}
          did={did}
          instituteId={instituteId}
        />
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default FeeStructureDetailMenu;
